.container-article {
  display: flex;
  justify-content: center;
  min-height: 95vh;
  height: auto;
}

.content {
  width: 50%;
  margin: 100px 0;
  padding: 20px;
  box-shadow: 1px 1px 5px black;
  border-radius: 3px;
}

.content h2 {
  margin-bottom: 20px;
}

.content p {
  text-align: left;
}

.App.dark .content h2 {
  color: white;
}
.App.dark .content p {
  color: var(--color3);
}

@media screen and (max-width: 600px) {
  .content {
    width: 80%;
  }
}
