h2 {
  text-align: center;
  margin: 30px 0;
  font-size: clamp(20px, 3vw, 30px);
}

body .App.dark {
  background-color: #3d3d3d;
  color: white;
}

.App.dark article a:visited {
  color: rgb(136, 136, 136);
}

.App.dark article a {
  color: darkblue;
}

.App.dark article .item {
  box-shadow: 0 0 5px black;
}
