.dark-btn {
  position: absolute;
  top: 100px;
  right: 20%;
  height: auto;
  border: 2px solid gray;
  border-radius: 50px;
  cursor: pointer;
  padding: 7px;
  background-color: rgb(245, 245, 245);
}

i.fa-sun {
  color: var(--color6);
}
i.fa-moon {
  color: var(--color4);
}

.dark-btn i {
  cursor: pointer;
}

button.dark-mode,
button.active-btn {
  border: none;
  background-color: transparent;
  font-size: 22px;
  position: relative;
  width: 50px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

button.dark-mode::after {
  content: "";
  display: block;
  background-color: #60accf;
  height: 30px;
  width: 30px;
  border-radius: 50px;
  position: absolute;
  top: -4px;
  left: -4px;
  pointer-events: none;
  animation: move-left 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.App.dark button.dark-mode::after {
  background-color: #22333b;
}

button.dark-mode.active-btn::after {
  left: 24px;
  animation: move-right 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

@keyframes move-left {
  0% {
    left: 24px;
  }
  100% {
    left: -4px;
  }
}

@keyframes move-right {
  0% {
    left: -4px;
  }
  100% {
    left: 24px;
  }
}

@media screen and (max-width: 700px) {
  .dark-btn {
    right: 20px;
  }
}
