article {
  margin: 0 20%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 250px);
  justify-content: center;
  grid-gap: 40px;
}

.home {
  padding: 50px 0;
}

.App.dark .home h2 {
  color: white;
}

article .item {
  box-shadow: 0 0 5px rgb(146, 146, 146);
  height: 150px;
  width: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px;
  transition: all 0.1s;
  opacity: 1;
  animation: appear 0.2s;
  background-color: #eae0d5;
}

.App.dark .home article .item {
  background-color: var(--color4);
}
.App.dark .home article .item h4 {
  color: var(--color3);
}

@keyframes appear {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

article .item a {
  color: #60accf;
}

article .item a:visited {
  color: #406c81;
}

.App.dark article .item a {
  color: #545d63;
}

.App.dark article .item a:visited {
  color: #080f13;
}
