nav {
  position: sticky;
  top: 0;
  display: flex;
  background-color: var(--color3);
  padding: 20px 20%;
  align-items: center;
  justify-content: space-between;
  z-index: 100;
  border-radius: 0 0 5px 5px;
  /* border-bottom: 2px solid var(--color2); */
  box-shadow: 0 1px 3px rgb(187, 187, 187);
}

nav .nav-link a {
  text-decoration: none;
  color: var(--color5);
  margin-left: 30px;
  transition: all 0.2s ease;
  width: fit-content;
  white-space: nowrap;
}

nav .nav-link a::after {
  content: "";
  display: block;
  height: 2px;
  width: 0;
  background-color: var(--color6);
  transition: width 0.2s cubic-bezier(0.645, 0.045, 0.355, 1.5);
  margin: 0px auto;
}

nav .nav-link a:hover::after {
  width: 80%;
}

nav .nav-link {
  display: flex;
}

nav .nav-link a:hover {
  color: var(--color6);
}

nav button {
  cursor: pointer;
  border: none;
  background: transparent;
  font-size: 20px;
  padding: 5px;
  display: none;
}

.small-nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

nav .small-nav a {
  text-decoration: none;
}

nav .nav-link a.current::after {
  content: "";
  display: block;
  height: 2px;
  width: 100%;
  color: var(--color6);
  background-color: var(--color6);
  transition: width 0.2s cubic-bezier(0.645, 0.045, 0.355, 1.5);
}

@media screen and (max-width: 600px) {
  nav,
  .nav-link {
    flex-direction: column;
  }
  nav .nav-link a {
    margin: 20px auto;
  }

  nav .small-nav button {
    display: block;
  }
  nav {
    padding: 20px 10%;
  }
}

.App.dark nav .small-nav a h1 {
  color: var(--color3);
}
.App.dark nav a {
  color: var(--color3);
}
.App.dark nav {
  box-shadow: 0 1px 5px var(--color5);
  background-color: var(--color4);
  color: var(--color3);
}
.App.dark nav .nav-link a:hover {
  color: var(--color3);
}

.App.dark nav .nav-link a.current::after {
  background-color: var(--color3);
}

.App.dark nav .nav-link a::after {
  background-color: var(--color3);
}
