.formulaire {
  min-height: 90vh;
}

form {
  display: flex;
  flex-direction: column;
  min-width: 200px;
  width: 70%;
  max-width: 700px;
  margin: auto;
}

.formulaire form label {
  margin-bottom: 5px;
}

.formulaire form input {
  margin-bottom: 20px;
  height: 30px;
}

.formulaire form textarea {
  height: 300px;
}

.formulaire button {
  height: 30px;
  cursor: pointer;
  border: none;
  border-radius: 0 0 5px 5px;
  background-color: var(--color3);
}

.formulaire form textarea,
.formulaire form input {
  border: 1px solid var(--color3);
  outline: none;
}
.formulaire form textarea:focus,
.formulaire form input:focus {
  outline: 1px solid var(--color2);
}

.App.dark .formulaire h2 {
  color: white;
}

.App.dark .formulaire form label {
  color: white;
}

.App.dark .formulaire form button {
  background-color: var(--color4);
  color: var(--color3);
}

.App.dark .formulaire form textarea,
.App.dark .formulaire form input {
  border: 1px solid var(--color4);
  background-color: var(--color3);
}

.App.dark .alert {
  color: white;
}
